.ag-blog-wrapper {
    margin-top: 70px;
    margin-bottom: 70px;
    margin-left: 20px;
    margin-right: 20px;
    height: 100%;
}

.ag-cardImage {
    width: 100%;
    height: 50%;
    -o-object-fit: cover;
       object-fit: cover
}

.ag-cardHead {
    text-align: left;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin-left: 3%;
    margin-right: 5%;
}

.ag-blog-card {
    background: #fff;
    border-radius: 2px;
    display: inline-block;
    height: 330px;
    margin-top: 20px;
    position: relative;
    width: 97%;
    -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    z-index: -5;
}

.ag-cardContent {
    text-align: justify;
    margin-left: 3%;
    margin-right: 5%;
}

/* unvisited link */
a:link {
    color: #333333;
}

/* visited link */
a:visited {
    color: #333333;
}

/* mouse over link */
a:hover {
    color: #333333;
}

/* selected link */
a:active {
    color: #333333;
}

@media only screen and (min-width: 720px) {
    .ag-blog-card {
        background: #fff;
        border-radius: 2px;
        display: inline-block;
        height: 500px;
        margin-top: 20px;
        position: relative;
        width: 97%;
        -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        z-index: -5;
    }
    .ag-cardImage {
        width: 100%;
        height: 70%;
        -o-object-fit: cover;
           object-fit: cover;
    }
    .ag-cardHead {
        text-align: left;
        -o-text-overflow: ellipsis;
           text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin-left: 3%;
        margin-right: 5%;
    }
    .ag-cardContent {
        text-align: justify;
        margin-left: 3%;
        margin-right: 5%;
    }
}

@media only screen and (max-width: 380px) {
    .ag-cardHead {
        text-align: left;
        -o-text-overflow: ellipsis;
           text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin-left: 3%;
        margin-right: 5%;
        font-size: 17px;
    }
    .ag-cardContent {
        text-align: justify;
        margin-left: 3%;
        margin-right: 5%;
        font-size: 14px;
    }
}


