.ag-blogpostwrapper{
    margin-top: 20px;
    width: 100%;
    -ms-flex-item-align: center;
        align-self: center;
}

.ag-blogpostHeadline{
    margin-top: 20px;
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
}

.ag-blogpostContent{
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 90px;
    text-align: justify;
}

.ag-blogpostImage{
    width: 100%;
    height: 400px;
    -o-object-fit: cover;
       object-fit: cover;
}


.close-button {
  height: 40px;
  width: 40px;
  margin-top: 55px;
  right: 20px;
  position: fixed;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 10px;
  display: inline-block;
  
}
.close-button:before,
.close-button:after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -5px;
  margin-left: -25px;
  display: block;
  height: 10px;
  width: 50px;
  background-color: lightcoral;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
.close-button:after {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close-button:hover:before,
.close-button:hover:after {
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}

button {
    background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
}




