/*  SECTIONS  */
.section {
    clear: both;
    padding: 0px;
    margin: 0px;
}

/*  COLUMN SETUP  */
.col {
    display: block;
    float:left;
    margin: 1% 0 1% 0.4%;
}
.col:first-child { margin-left: 0; }

/*  GROUPING  */
.group:before,
.group:after { content:""; display:table; }
.group:after { clear:both;}
.group { zoom:1; /* For IE 6/7 */ }


/*  GRID OF TWELVE  */
.span_12_of_12 {
    width: 100%;
}

.span_11_of_12 {
    width: 91.63%;
}
.span_10_of_12 {
    width: 83.26%;
}

.span_9_of_12 {
    width: 74.9%;
}

.span_8_of_12 {
    width: 66.53%;
}

.span_7_of_12 {
    width: 58.16%;
}

.span_6_of_12 {
    width: 49.8%;
}

.span_5_of_12 {
    width: 41.43%;
}

.span_4_of_12 {
    width: 33.06%;
}

.span_3_of_12 {
    width: 24.7%;
}

.span_2_of_12 {
    width: 16.33%;
}

.span_1_of_12 {
    width: 7.966%;
}

/*  GO FULL WIDTH BELOW 480 PIXELS */
@media only screen and (max-width: 120px) {
    .col {  margin: 1% 0 1% 0%; }

    .span_1_of_12, .span_2_of_12, .span_3_of_12, .span_4_of_12, .span_5_of_12, .span_6_of_12, .span_7_of_12, .span_8_of_12, .span_9_of_12, .span_10_of_12, .span_11_of_12, .span_12_of_12 {
        width: 100%;
    }
}



